        </div>
        <button class="quit" @click="aquit()">退出登录</button>
        <!-- 弹出框 -->
            <div class="modals" v-if="!warnbol">
                <div class="warn">
                    <span></span>
                    <span></span>
                </div>
                <div class="warn-con">是否退出登录</div>
                <div class="btn">
                    <div class="btn1" @click="bquit()">再等等</div>
                    <div class="btn2" @click="quit()">退出登录</div>
                </div>
            </div>
    </div>
</template>

<script>
import Head from "./../header/Head";
import axios from "axios"
axios.defaults.withCredentials=true
export default {
  components: {
    Head
  },
  data() {
    return {
      users: false,
      photo: "",
      userimg: "",
      usertel:'',
      warnbol:true,
      uid:0,
    };
  },
  methods: {
    //选择事件时，会发出change事件
    getimg() {
      let  input = this.$refs.inp
      console.log(input.files[0])
      //formData数据类型保存文件图片
      let data = new FormData()
      data.append('file',input.files[0]) //保存的是文件类型 具体的文件
      //设置请求头 content-type类型
      console.log(this.uid)
      let heades = {heades:{'Content-Type':'multipart/from-data'}}
      axios.post('http://cangdu.org:8001/eus/v1/users/'+this.uid+'/avatar',data,{heades:{'Content-Type':'multipart/from-data'}}).then(res=>{
        console.log(res)
        if(res.status==1){
          this.userimg=res.image_path
          this.$store.commit('addimg',this.userimg)
        }
      })
    },
    //弹出框
    aquit(){
        this.warnbol=false;
    },
    bquit(){
        this.warnbol=true;
    },
    //退出登陆
    quit() {
        var that = this
        this.http.get('http://elm.cangdu.org/v2/signout').then(res=>{
          console.log(res)
          if(res.message=='退出成功'){
            this.$store.commit('aa')
            that.$router.go(-1)
          }
        })
      }
  },
  computed: {},
  mounted() {
    if (localStorage.getItem("vuex")) {
      var Arr = JSON.parse(localStorage.getItem("vuex"));
      for (let i in Arr.users) {
        this.userimg = Arr.users[i].avatar;
        this.usertel = Arr.users[i].name;
        this.uid = Arr.users[i].id
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  width: 100%;
  height: 100%;
  padding-top: 0.98rem;
  .account {
    width: 100%;
    background: white;
