        width: 100%;
        height: 1.44rem;
        border: none;
      }
      p {
        position: absolute;
        left: 0.2rem;
        top: 0.5rem;
        font-size: 0.3rem;
      }
      img {
        width: 0.93rem;
        height: 0.93rem;
        border-radius: 50%;
        position: absolute;
        left: 5.9rem;
        top: 0.26rem;
      }
      .icon {
          width: 0.5rem;
          height: 0.3rem;
          position: absolute;
          left: 6.9rem;top: 0.57rem;
      }
    }
    .username {
        width: 100%;
        height: 0.94rem;
        border-bottom: 0.01rem solid #ddd;
        display: flex;
        p {
            font-size: 0.3rem;
