          width: 0.5rem;
          height: 0.3rem;
          margin-left: 0.05rem;
          margin-top: 0.33rem;
      }
    }
    .site {
        width: 100%;
        height: 0.94rem;
        border-bottom: 0.01rem solid #ddd;
        display: flex;
        position: relative;
        p {
            font-size: 0.3rem;
            line-height: 0.94rem;
            margin-left: 0.2rem;
        }
        span {
            font-size: 0.34rem;
            color: #999;
            position: absolute;
            left: 6.2rem;top: 0.22rem;
        }
        .icon {
          width: 0.5rem;
          height: 0.3rem;
          margin-left: 5.5rem;
          margin-top: 0.33rem;
         }
    }
    .bound{
        width: 7.1rem;
        height: 0.32rem;
        padding: 0.18rem 0.2rem;
        background: #f1f1f1;
        font-size:0.24rem;
        text-align: left;
    }
    .boundtel {
        width: 7.5rem;
