  }
}
.quit {
    width: 7.2rem;
    height: 0.7rem;
    background: #d8584a;
    color: white;
    border: none;
    outline: none;
    font-size: 0.4rem;
    border-radius: 0.1rem;
}
.modals{
      width: 7rem;
      height: 4rem;
      position: fixed;
      left: 0.25rem;top: 2rem;
      background: white;
      z-index: 5;
      border-radius: 0.1rem;
      border: 0.01rem solid #f5f5f5;
      .warn {
          width: 1.4rem;
          height: 1.4rem;
          border-radius: 50%;
          border: 0.07rem solid #f8cb86;
          margin: 0 auto;
          margin-top: 0.28rem;
          span:nth-child(1){
              width: 0.06rem;
              height: 0.7rem;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.2rem;
          }
          span:nth-child(2){
              width: 0.1rem;
              height: 0.1rem;
              border-radius: 50%;
              background: #f8cb86;
              display: block;
              margin: 0 auto;
              margin-top: 0.1rem;
          }
      }
      .warn-con{
          width: 4.26rem;
