          margin: 0 auto;
          margin-top: 0.38rem;
          font-size: 0.36rem;
      }
      .btn {
          width: 5rem;
          height: 0.74rem;
          margin: 0 auto;
          margin-top: 0.3rem;
          display: flex;
          .btn1 {
              width: 1.8rem;
              height: 0.74rem;
              border-radius: 0.1rem;
              color: white;
              background: #c1c1c1;
              font-size: 0.3rem;
              line-height: 0.74rem;
              margin-left: 0.26rem;
          }
          .btn2 {
              width: 2.1rem;
              height: 0.74rem;
              color: white;
              background: #d8584a;
              line-height: 0.74rem;
              font-size: 0.3rem;
              margin-left: 0.5rem;
              border-radius: 0.1rem;
